  }),
  computed: {
    ...mapGetters(["UserProfile"]),

    isUser() {
      if (localStorage.getItem("user-token")) return true;
      return false;
    },
    profile() {
      if (this.UserProfile && this.UserProfile._id) return this.UserProfile;
      else return this.AdminProfile;
    },
  },
  mounted() {
